<template>
    <div id="app" :class="isrotate?'rotate':''">
      <app-back ref="back" :restoreApp="restoreApp"/>
      <drop-list 
        :dropShow="dropShow" 
        :autoSave="autoSave" 
        :autoLogin="autoLogin"
        :changeAuLog="changeAuLog"
        :changeSave="changeSave"
        :rotateApp="rotateApp"
        v-show="isDropShow"
      />
      <app-face 
        :changeDrop="changeDrop" 
        :autoSave="autoSave" 
        :autoLogin="autoLogin"
        :get_user_pass="get_user_pass"
        :loginOk="loginOk"
      />
    </div>
</template>

<script>

import appFace from "./components/appFace.vue"
import appBack from "./components/appBack.vue"
import dropList from "./components/faceVc/dropList.vue"

export default {
  name:"Login",
  props:["loginOk","getId"],
  components:{
    appFace,
    appBack,
    dropList,
    
  },
  data() {
    return {
      dropShow:false,
      autoSave:localStorage.getItem("autoSave") === "true"? true : false,
      autoLogin:localStorage.getItem("autoLogin") === "true"? true : false,
      user:"",
      pass:"",
      isrotate:false,
      isDropShow:true,
      requestUrl:"http://gxd.newimg.ltd:8099/logIn",
    }
  },
  methods: {
    changeDrop(){
      this.dropShow = !this.dropShow;
    },
    changeAuLog(){
      this.autoLogin = !this.autoLogin;
    },
    changeSave(){
      this.autoSave = !this.autoSave;
    },
    get_user_pass(user,pass){
      [this.user,this.pass] = [user,pass];
    },
    rotateApp(){
      this.isrotate = !this.isrotate;
      this.isDropShow = !this.isDropShow;
      setTimeout(()=>{
        this.$refs.back.$el.style.zIndex = 2;
      },500);
    },
    restoreApp(){
      this.isrotate = !this.isrotate;
      setTimeout(()=>{
        this.isDropShow = !this.isDropShow;
        this.$refs.back.$el.style.zIndex = 0;
      },500);
    },
    login(){
            const poststr = `${this.requestUrl}?username=${this.user}&password=${this.pass}`;
            this.ajax(poststr,(res)=>{
                console.log(res);
                if(res.code === 0){
                    // console.log("登陆成功,跳转页面");
                    localStorage.setItem("token",res.data);
                    setTimeout(()=>{
                      this.loginOk();
                    },1000);
                }else{
                    alert("用户不存在或密码错误");
                }
            })
            
    },
    ajax(url, fun) {
            const xhr = new XMLHttpRequest();
            xhr.onreadystatechange = () => {
                if (xhr.status == 200 && xhr.readyState == 4) {
                    fun(JSON.parse(xhr.responseText));
                }
            }
            xhr.open("POST", url, true);
            // xhr.withCredentials = true;
            xhr.send();
    },
  },
  watch:{
    autoSave:{
      immediate:true,
      handler(val){
        localStorage.setItem("autoSave",val);
      }
    },
    autoLogin:{
      immediate:true,
      handler(val){
        localStorage.setItem("autoLogin",val);
      }
    },
    user:{
      immediate:true,
      handler(val,pre){
        if(val && this.autoLogin &&!pre){
          // console.log("自动登录",this.user,this.pass);
          this.login();
        }
        this.getId(val);
      }
    }
  }
}
</script>

<style scoped>
  #app {
      width: 251px;
      height: 315px;
      background-color: white;
      position: relative;
      margin-top: calc(50vh - 157.5px);
      left: calc(50% - 125.5px);
      border-radius: 4px;
      transition: all 1s ease-in-out;
  }

  .rotate{
    transform: rotateY(180deg);
  }
</style>