<template>
    <div class="YetWrapper" >
        
        <todo-item-end 
            
            v-for="(todo,index) in listYet"
            :key = "todo.id"
            :title="todo.title"
            :index = "index + 1"
         />
    </div>
</template>

<script>
import todoItemEnd from './todoItem_end.vue'

export default {
    name:"todoYet",
    props:["todos"],
    components:{
        todoItemEnd,
    },
    computed:{
        listYet(){
            return this.todos.filter((todo)=>{
                return !todo.done;
            });
        }
    }
}
</script>

<style scoped>
    .YetWrapper{
        width: 90%;
        margin: 0 auto;
    }
</style>