<template>
    <div class="formbox">
            <div class="user-box">
                <input type="text" :placeholder="isUfocus?'':'输入账号'" v-model="user" @focus="hideUplacehoder"  @blur="showUplacehoder" @keyup.enter="login">
            </div>
            <div class="pass-box">
                <input type="password" :placeholder="isPfocus?'':'输入密码'" v-model="password" @focus="hidePplacehoder" @blur="showPplacehoder" @keyup.enter="login">
                <div class="submit-btn" @click="login" @mousedown="beCyan" @mouseup="beNormal"><i class="iconfont">&#xe62d;</i></div>
            </div>
    </div>
</template>

<script>
export default {
    name:"faceForm",
    props:["autoSave","autoLogin","get_user_pass","loginOk"],
    data() {
        return {
            user:localStorage.getItem("user")||"",
            password:localStorage.getItem("password")||"",
            isUfocus:false,
            isPfocus:false,
            requestUrl:"http://gxd.newimg.ltd:8099/logIn",
        }
    },
    methods: {
        showEle(e){
            console.log(e.target);
        },
        showUplacehoder(){
            this.isUfocus = false;
        },
        showPplacehoder(){
            this.isPfocus = false;
        },
        hideUplacehoder(){
            this.isUfocus = true;
        },
        hidePplacehoder(){
            this.isPfocus = true;
        },
        login(){
            if(!this.user||!this.password) return alert("请输入啊你倒是");
            const poststr = `${this.requestUrl}?username=${this.user}&password=${this.password}`;
            this.ajax(poststr,(res)=>{
                console.log(res);
                if(res.code === 0){
                    console.log("登陆成功,跳转页面");
                    localStorage.setItem("token",res.data);
                    this.loginOk();
                }else{
                    alert("用户不存在或密码错误");
                }
            })
            // console.log("手动登陆",this.user,this.password);
            if(this.autoSave){
                localStorage.setItem("user",this.user);
                localStorage.setItem("password",this.password);
            }
        },
        beCyan(e){
            e.target.style.color = "cyan"
        },
        beNormal(e){
            e.target.style.color = ""
        },
        ajax(url, fun) {
            const xhr = new XMLHttpRequest();
            xhr.onreadystatechange = () => {
                if (xhr.status == 200 && xhr.readyState == 4) {
                    fun(JSON.parse(xhr.responseText));
                }
            }
            xhr.open("POST", url, true);
            // xhr.withCredentials = true;
            xhr.send();
        }
    },
    watch:{
        user:{
            immediate:true,
            handler(){
                this.get_user_pass(this.user,this.password);
            }
        }
    }
}
</script>

