<template>
  <div class="drop-btn" @mousedown="downEv" @mouseup="upEv" @touchstart="downEv" @touchend="justColor"><i class="iconfont">&#xe6cc;</i></div>
</template>

<script>
export default {
    name:"faceFooter",
    props:["changeDrop"],
    methods: {
      downEv(e){
        e.target.style.color = "cyan"
      },
      upEv(e){
        e.target.style.color = ""
        this.changeDrop();
      },
      justColor(e){
        e.target.style.color = ""
      }
    },
}
</script>

<style>

.drop-btn {
    width: 2em;
    text-align: center;
    margin: 0 auto;
    margin-top: 20px;
}

</style>