<template>
    <div class="doneWrapper">
        <todo-item-end  
            v-for="(todo,index) in listDone"
            :key = "todo.id"
            :title="todo.title"
            :index = "index + 1"
         />
         
    </div>
</template>

<script>

import todoItemEnd from "./todoItem_end.vue"

export default {
    name:"todoDone",
    props:["todos"],
    components:{
        todoItemEnd,
    },
    computed:{
        listDone(){
                return this.todos.filter((todo) => { 
                    return todo.done === true;
                }
            );
        }
    }
}
</script>

<style scoped>
    .doneWrapper{
        width: 90%;
        margin: 0 auto;
    }
</style>