<template>
  <div class="formbox" style="margin-top:15px;">
    <div class="user-box">
        <input type="text" name="user" id="user-sign" :placeholder="in1focus?'输入账号':''" v-model="user" @keyup.enter="signup" @focus="change1" @blur="change1">
    </div>
    <div class="pass-box">
        <input type="text" name="emial" id="email-sign" :placeholder="is4focus?'输入邮箱':''" v-model="email" @keyup.enter="signup" @focus="change4" @blur="change4">
    </div>
    <div class="pass-box">
        <input type="password" name="pass" id="pass-sign" :placeholder="in2focus?'输入密码':''" v-model="pass1" @keyup.enter="signup" @focus="change2" @blur="change2">
    </div>
    <div class="pass-box">
        <input type="password" name="pass" id="pass-sign-confirm" :placeholder="in3focus?'确认密码':''" v-model="pass2" @keyup.enter="signup" @focus="change3" @blur="change3">
        <div class="submit-btn" @click="signup" @mousedown="changeColor" @touchstart="changeColor" @mouseup="resetColor" @touchend ="resetColor"><i class="iconfont">&#xe62d;</i></div>
    </div>
  </div>
</template>

<script>
export default {
    name:"backForm",
    props:[],
    data() {
      return {
        in1focus:true,
        in2focus:true,
        in3focus:true,
        is4focus:true,
        user:"",
        pass1:"",
        pass2:"",
        email:"",
        requestUrl:"http://gxd.newimg.ltd:8099/signUp",
      }
    },
    methods: {
      signup(){
        if(!this.user||!this.pass1||!this.pass2){
          return alert("请输入完整的表单");
        }
        if(this.pass1 !== this.pass2){
          this.pass1 = this.pass2 = "";
          return alert("两次密码不一致,请重新输入");
        }
        const postStr = `${this.requestUrl}?username=${this.user}&password=${this.pass1}&email=${this.email}`;
        this.ajax(postStr,(res)=>{
          console.log(res);
          if(res.code === 0){
            return alert("注册成功,请登陆");
          }
        })
      },
      change1(){
        this.in1focus = !this.in1focus;
      },
      change2(){
        this.in2focus = !this.in2focus;
      },
      change3(){
        this.in3focus = !this.in3focus;
      },
      change4(){
        this.is4focus = !this.is4focus;
      }, 
      changeColor(e){
        e.target.style.color = "cyan"
      },
      resetColor(e){
        e.target.style.color = ""
      },
      ajax(url, fun) {
        const xhr = new XMLHttpRequest();
        xhr.onreadystatechange = () => {
          if (xhr.status == 200 && xhr.readyState == 4) {
              fun(JSON.parse(xhr.responseText));
          }
        }
        xhr.open("POST", url, true);
        // xhr.withCredentials = true;
        xhr.send();
      }
    },
}
</script>

