<template>
  <div class="endHeader">
      <div class="input_wrapper"><input type="text" name="" id="" placeholder="输入查找日期如2020-11-01" v-model="targetDate" @keyup.enter="sendToFind"></div>
      <div class="btn_find" @click="sendToFind"><span>查找</span></div>
  </div>
</template>

<script>
export default {
    name:"endListHeader",
    props:["findTodos","id"],
    methods: {
        thisDate(){
            const date = new Date();
            return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
        },
        sendToFind(){
            this.findTodos(this.id+"-todo-in-"+this.targetDate);
        }
    },
    data() {
        return {
            targetDate:this.thisDate(),
        }
    },
}
</script>

