<template>
  <div id="App">
    <login v-if="!isLogin" :loginOk="loginOk" :getId="getId"/>
    <todo v-else :backLogin="backLogin" :id="id"/>
  </div>
</template>

<script>
import todo from "./components/todoCompents/todo.vue"
import Login from "./components/loginCompents/login.vue"

export default {
  name: 'App',
  components: {
    todo,
    Login,
  },
  data() {
    return {
      isLogin:false,
      id:"",
    }
  },
  methods:{
    loginOk(){
      this.isLogin = true;
    },
    backLogin(){
      this.isLogin = false;
      localStorage.setItem("autoLogin","false")
    },
    getId(val){
      this.id = val;
    }
  }
}
</script>

<style>
#App{
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left:0;
}
</style>
