<template>
  <div class="oneItem"  @mouseover="showDel" @mouseleave="hideDel2" >
      <div class="done"><input type="checkbox" name="" id="" :checked = "done" @click="checkTodo(id)"></div>
      <div class="todoName"  @click="hideDel">{{title}}</div>
      <div class="delBtn" v-if = "isMouseOn" @touchend = "delTodo(id)" @click = "delTodo(id)"><span>删除</span></div>
  </div>
</template>

<script>
export default {
    name:"todoItem",
    props:["title","done","handerChange","checkTodo","id","delTodo"],
    data() {
        return {
            isMouseOn:false,
            timer:"",
        }
    },
    methods: {
        showDel(){
            this.isMouseOn = true;
        },
        hideDel2(){
            this.isMouseOn = false;
        },
        hideDel(){
            this.isMouseOn = !this.isMouseOn;
        },
        beginCount(e){
            e.preventDefault();
            // console.log("开始触摸")
            this.timer = setTimeout(
                ()=>{
                    if(this.isMouseOn){
                        this.hideDel();
                    }else{
                        this.showDel();
                    }
                },
                1000
            );
        },
        endCount(){
            // console.log("结束触摸");
            clearTimeout(this.timer);
        },
        checkThis(e){
            e.target.checked = !e.target.checked;
        }
    },
}
</script>

