<template>
  <div id="app" >
      <div v-if="!isEnd">
        <todo-header 
          :receive = "receive"
          />
        <todo-list 
          :todos = "todos"  
          :checkTodo = "checkTodo" 
          :delTodo = "delTodo"
          />
        <todo-footer 
          v-show = "todos.length" 
          :allCheck = "allCheck" 
          :isallCheck = "isallCheck" 
          :length = "todos.length"
          :haveDone = "haveDone"
          :endAll="endAll"
          />
      </div>
      <div class="endListShow" v-else>
          <end-list-header :findTodos="findTodos" :id="id"/>
          <h4>已完成列表</h4>
          <todo-done :todos ="showTodos"/>
          <h4>未完成列表</h4>
          <todo-yet :todos ="showTodos"/>
      </div>
      <jump-history v-if = "canChange" :jumpPast="jumpPast"/>
      <div class="backLogin" @click="backLogin">返回登陆</div>
  </div>

</template>

<script>

import todoHeader from './components/todoHeader.vue'
import todoList from './components/todoList.vue'
import todoFooter from './components/todoFooter.vue'
import todoDone from './components/todoDone.vue'
import todoYet from './components/todoYet.vue'
import endListHeader from './components/endListHeader.vue'
import jumpHistory from './components/jumpToHistory.vue'

export default {
  name: 'todo',
  components: {
    todoHeader,
    todoList,
    todoFooter,
    todoDone,
    todoYet,
    endListHeader,
    jumpHistory
  },
  props:["backLogin","id"],
  data(){
    return{
      todos:JSON.parse(localStorage.getItem(this.todoDate())) || [],
      isEnd:localStorage.getItem("canChange-"+this.todoDate())=== "false"?true:false,
      canChange:localStorage.getItem("canChange-"+this.todoDate())=== "false"?false:true,
      showTodos:JSON.parse(localStorage.getItem(this.todoDate()))||[],
      requestUrl:"http://gxd.newimg.ltd:8099/uploadList",
      requestUrl2:"http://gxd.newimg.ltd:8099/getList",
    }
  },
  methods: {
    receive(todo){
      this.todos.unshift(todo);
    },
    checkTodo(id){
      this.todos.forEach(todo => {
        if(todo.id === id) todo.done = !todo.done;
      });
    },
    delTodo(id){
      this.todos = this.todos.filter( (todo) => {
        return todo.id != id;
      });
    },
    alldone(){
      this.todos.forEach(todo => todo.done = true);
    },
    allcancel(){
      this.todos.forEach(todo => todo.done = false);
    },
    allCheck(checked){
      console.log(checked);
      if(checked){
        this.alldone();
      }else{
        this.allcancel();
      }
    },
    endAll(){
      if(confirm("确定要结束今天的任务状态吗？") === false){
        return
      }
      this.isEnd = true;
      this.canChange = false;
      localStorage.setItem("canChange-"+this.todoDate(),false);
      this.showTodos = JSON.parse(localStorage.getItem(this.todoDate()))||[];
    },
    todoDate(){
        const date = new Date();
        return `${this.id}-todo-in-${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
    },
    jumpPast(){
      this.showTodos = JSON.parse(localStorage.getItem(this.todoDate()))||[];
      this.isEnd = !this.isEnd;
    },
    findTodos(tododate){
      // this.showTodos = JSON.parse(localStorage.getItem(tododate))||[];
      this.getList(tododate);
    },
    ajax(url, fun) {
            const xhr = new XMLHttpRequest();
            xhr.onreadystatechange = () => {
                if (xhr.status == 200 && xhr.readyState == 4) {
                    fun(JSON.parse(xhr.responseText));
                }
            }
            xhr.open("POST", url, true);
            // xhr.withCredentials = true;
            xhr.send();
    },
    sendList(val){
      const parmeObj = {};
      parmeObj.token = localStorage.getItem("token");
      parmeObj.date = this.todoDate();
      parmeObj.list = JSON.stringify(val);
      let newListStr = "";
      for(let i in parmeObj.list){
          if(parmeObj.list[i] === '['){
              newListStr += '_mbl';
          }else if(parmeObj.list[i] === ']'){
              newListStr += '_mbr';
          }else if(parmeObj.list[i] === '{'){
              newListStr += '_cbl';
          }else if(parmeObj.list[i] === '}'){
              newListStr += '_cbr';
          }else if(parmeObj.list[i] === ':'){
              newListStr += '_mao';
          }else{
            newListStr += parmeObj.list[i];
          }
      }
      const postUrl = `${this.requestUrl}?token=${parmeObj.token}&date=${parmeObj.date}&list=${newListStr}`;
      this.ajax(postUrl,(res)=>{
        if(res.code === 0){
          console.log("上传成功");
        }else{
          console.log(res,"上传失败");
        }
      });
    },
    getList(tododate){
      const postStr = `${this.requestUrl2}?token=${localStorage.getItem("token")}&date=${tododate}`;
      this.ajax(postStr,(res)=>{
          if(res.code === -1){
            this.showTodos = JSON.parse(localStorage.getItem(tododate))||[];
            return console.log("系统繁忙");
          }
          if(res.code === 0 && !res.data){
            return alert("那一天你还没有设置任务哦");
          }
          let newTods = res.data.list;
          newTods = newTods.replaceAll("_mbl",'[')
          newTods = newTods.replaceAll("_mbr",']')
          newTods = newTods.replaceAll("_cbl",'{')
          newTods = newTods.replaceAll("_cbr",'}')
          newTods = newTods.replaceAll("_mao",':')
          console.log(newTods);
          this.showTodos = JSON.parse(newTods);
          localStorage.setItem(tododate,newTods);
          console.log(tododate,this.todoDate())
          if(tododate === this.todoDate()){
            this.todos = JSON.parse(newTods);
          }
      });
    }
  },
  computed:{
    isallCheck(){
      return this.todos.every((todo)=>{
        return todo.done === true;
      })
    },
    haveDone(){
      return this.todos.reduce((pre,todo) =>{
        return todo.done ? pre + 1 : pre;
      },0);
    }
  },
  watch:{
    todos:{
      deep:true,
      immediate:true,
      handler(val){
        if(val.length === 0) {
          return this.getList(this.todoDate());
        }
        localStorage.setItem(this.todoDate(),JSON.stringify(val));
        this.sendList(val);
      }
    }
  }
}
</script>

