<template>
    <div class="todohead">
        <div class="todoInput">
            <input 
                type="text" name="" id="" 
                v-model="title" 
                :placeholder="isBlur?'输入你要设置的任务':''" 
                @keyup.enter = "addTodo"
                
            >
        </div>
        <div class="addBtn" @click="addTodo"><span>添加</span></div>
    </div>
</template>

<script>

import {nanoid} from "nanoid"

export default {
    name:"todoHeader",
    props:["receive"],
    methods: {
        addTodo(){
            if(!this.title.trim()) return alert("输入为空,请重新输入");
            const todoObj = {id:nanoid(),title:this.title,done:false};
            this.receive(todoObj);
            this.title = "";
        }
    },
    data() {
        return {
            title:"",
            isBlur:true,
        }
    },
}
</script>

