<template>
    <div class="btnJump" @click="jumpPast" @touchstart="startJump" @touchend="endJump">
        <span>回溯</span>
    </div>
</template>

<script>

export default {
    name:"jumpHistory",
    props:["jumpPast"],
    methods: {
        startJump(){
            this.timer = setTimeout(()=>{
                this.jumpPast();
            },1000);
        },
        endJump(){
            clearTimeout(this.timer);
        },

    },
    data() {
        return {
            timer:"",
        }
    },
}
</script>

<style scoped>
    .btnJump{
        width: 4em;
        height: 4em;
        background-color: white;
        display: grid;
        place-content: center;
        position: fixed;
        top: calc(50% - 2em);
        left: -2em;
        border-radius: 2em;
        opacity: 0.7;
        box-shadow: -3px -3px 7px #ffffff73, 2px 2px 5px rgb(94 104 121 / 29%);
    }
</style>